.container {
    top: 0;
    left: 0;
    height: 130rem;
    width: 100%;
    background-image: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.3),
        rgba(0, 0, 0, 0.452)
      ),
      url("../Media/phase2back.jpg");
    background-size: cover;
    background-repeat: none;
    background-position: center;
    max-width: 100%;
    color: ghostwhite;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .main{
      display: flex;
      justify-content: center;
      align-items: center;
  }